
import { api } from '@/modules/api'
import { useAuth } from '@/modules/auth'
import { useNotification } from '@/modules/notification'
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'MovieGridItem',
  setup() {
    const { setNotification } = useNotification()
    const { authenticated } = useAuth()

    return {
      authenticated,
      setNotification,
    }
  },
  props: {
    to: Object,
    id: Number,
    poster: String,
    rating: Number,
    imdbRating: Number,
    title: String,
    list: [Object],
    favorite: Boolean,
  },
  data: function() {
    return {
      isFavorite: this.favorite === true
    }
  },
  methods: {
    toggleFavorite() {
      console.log(`/account/favorites/${this.id}`)

      if (this.isFavorite) {
        api.delete(`/account/favorites/${this.id}`)
          // eslint-disable-next-line
          .then((res: any) => {
            this.isFavorite = res.data.favorite

            this.setNotification('success', 'This movie has been removed from your favorites')
          })
      } else {
        api.post(`/account/favorites/${this.id}`)
          // eslint-disable-next-line
          .then((res: any) => {
            this.isFavorite = res.data.favorite

            this.setNotification('success', 'This movie has been added to your favorites')
          })
      }
    },
  },
})
